<template>
  <div class="change-file-model">
    <!-- v-if   -->
    <el-radio-group
      v-if="fileType === 1"
      v-model="currentshowModel"
      size="mini"
    >
      <el-radio-button :label="0">
        <i class="el-icon-tickets"></i> 
      </el-radio-button>
      <el-radio-button :label="1">
        <i class="el-icon-s-grid"></i> 
      </el-radio-button>
      <el-radio-button :label="2">
        <i class="el-icon-date"></i> 
      </el-radio-button>
    </el-radio-group>
  </div>
</template>

<script>
export default {
  name: "ShowModel",
  props: {
    // 
    fileType: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {};
  },
  computed: {
    //   - 0  | 1  | 2 
    currentshowModel: {
      get() {
        return this.$store.getters.showModel;
      },
      set(val) {
        this.$store.commit("changeShowModel", val); //  
      },
    },
  },
};
</script>

<style lang="stylus" scoped>
.change-file-model {
  margin-right: 16px;
}
</style>