import axios from "axios";
import { Message } from "element-ui";
import Cookies from "js-cookie";

// 
axios.defaults.timeout = 10000 * 5;
// URL
axios.defaults.baseURL = "/api";
// post
axios.defaults.headers.post["Content-Type"] = "application/json;charset=utf-8";


// 
axios.interceptors.request.use(
  (config) => {
    //    
    config.headers["token"] = Cookies.get("token");
    //console.log(config);
    return config;

  },
  (error) => {
    //
    console.log(error);//
    return Promise.reject(error);
  }
);


// 
axios.interceptors.response.use(
  (response) => {
    if (response.status === 200) {
      //    HTTP200
      return Promise.resolve(response);
    }
  },
  // HTTP200
  (error) => {
    if (error.response.status) {
      switch (error.response.status) {
        case 500: //    HTTP500
          Message.error("");
          break;
        case 401: //    HTTP401
          Message.error("");
          break;
        case 404: //    HTTP404
          Message.error("");
          break;
        default: //    
          this.$message.error(error.response.message);
          return Promise.reject(error.response);
      }
    }
  }
);


/**
 * getget
 */
export function get(url, params, info = "") {
  console.log(url);
  return new Promise((resolve, reject) => {
    axios
      .get(url + info, {
        params: params,
      })
      .then((res) => {
        resolve(res.data); //    
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}

/**
 * postpost
 * info trueformData
 * info undefinedfalsejson
 */
export function post(url, data = {}, info) {
  return new Promise((resolve, reject) => {
    let newData = data;
    if (info) {
      //  formData
      newData = new FormData();
      for (let i in data) {
        newData.append(i, data[i]);
      }
    }
    axios
      .post(url, newData)
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}

/**
 * put
 */

export function put(url, params = {}, info = "") {
  return new Promise((resolve, reject) => {
    axios.put(url + info, params).then(
      (res) => {
        resolve(res.data);
      },
      (err) => {
        reject(err.data);
      }
    );
  });
}

/**
 * delete
 */
export function axiosDelete(url, params = {}, info = "") {
  return new Promise((resolve, reject) => {
    axios
      .delete(url + info, {
        params: params,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}