<template>
  <div class="bread-crumb-wrapper">
    <div class="current-path"></div>
    <!--  -->
    <el-breadcrumb class="bread-crumb" v-if="fileType" separator="/">
      <el-breadcrumb-item>{{ fileTypeMap[fileType] }}</el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-breadcrumb class="bread-crumb" v-else separator="/">
      <!--  -->
      <el-breadcrumb-item
        v-for="(item, index) in breadCrumbList"
        :key="index"
        :to="{ query: { fileType: 0, filePath: item.path } }"
        >{{ item.name }}</el-breadcrumb-item
      >
    </el-breadcrumb>
  </div>
</template>

<script>
export default {
  name: "BreadCrumb",
  props: {
    fileType: {
      type: Number,
      required: true,
    },
  },
  computed: {
    //  data  breadCrumbList 
    breadCrumbList() {
      let path = this.$route.query.filePath //   //
      let pathList = path ? path.split('/').slice(1, -1) : [] //  ~ 
      let res = [{ name: '', path: '/' }]
      pathList.forEach((element, index) => {
        res.push({
          name: element,
          path: `${res[index].path}${element}/`
        })
      })
      return res
    }
  },
  data() {
    return {
      fileTypeMap: {
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6:"",
      },
      // //  
      // breadCrumbList: [
      //   { name: "", path: "/" },
      //   { name: "", path: "//" },
      // ],
    };
  },
};
</script>

<style lang="stylus" scoped>
.bread-crumb-wrapper {
  height: 32px;
  line-height: 32px;
  display: flex;
  align-items: center;
}
</style>