<template>
  <div class="select-column">
    <el-button
      type="info"
      size="mini"
      plain
      icon="el-icon-s-operation"
      @click="handleClickSelectColumn"
      ></el-button
    >
    <!--  "" -->
    <el-dialog
      title=""
      width="700px"
      :visible.sync="dialogVisible"
    >
      <!--   -->
      <el-checkbox-group v-model="selectedColumn">
        <el-checkbox
          v-for="item in columnOptions"
          :key="item.value"
          :label="item.value"
          >{{ item.label }}</el-checkbox
        >
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogOk()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script >
export default {
  name: "SelectColumn",
  props: {},
  data() {
    return {
      dialogVisible: false,
      selectedColumn: [], //  
      columnOptions: [
        {
          value: "extendName",
          label: "",
        },
        {
          value: "fileSize",
          label: "",
        },
        {
          value: "uploadTime",
          label: "",
        },
      ],
    };
  },
  methods: {
    //  - 
    handleClickSelectColumn() {
      // 1. store
      this.selectedColumn = this.$store.getters.selectedColumnList
      this.dialogVisible = true
    },
    //  
    dialogOk() {
      // 2.  mutation 
      this.$store.commit('changeSelectedColumnList', this.selectedColumn)
      this.dialogVisible = false
      //console.log(this.selectedColumn)
    }
  }
};
</script>