import Vue from "vue";
import Vuex from "vuex";
import user from "./module/user"; //  user.js
import file from "./module/file"; //  file 
import imgReview from "./module/imgReview"; //   imgReview 

Vue.use(Vuex);

export default new Vuex.Store({
  state: {},
  getters: {
    // 
    isLogin: (state) => state.user.isLogin,
    // 
    username: (state) => state.user.username,
    // id
    userId: (state) => state.user.userId,
    // 
    userInfoObj: (state) => state.user.userInfoObj,
    // 
    selectedColumnList: (state) =>
      state.file.selectedColumnList === null
        ? state.file.allColumnList
        : state.file.selectedColumnList.split(","),

    //  - 0  | 1  | 2 
    showModel: (state) => (state.file.showModel === null ? 0 : Number(state.file.showModel))
  },
  mutations: {
    //
  },
  actions: {
    //
  },
  modules: {
    user,
    file,
    imgReview,
  },
});