<template>
  <div class="operation-menu-wrapper">
    <!--  -->
    <el-button-group class="operate-group">
      <!-- disabled   -->
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-plus"
        v-show="fileType!=6"
        :disabled="fileType !== 0"
        @click="addFolderDialog.visible = true"></el-button>
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-upload2"
        v-show="fileType!=6"
        :disabled="fileType !== 0"
        @click="handleUploadFileClick()"></el-button>
        <!-- disabled  -->
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-delete"
        v-show="fileType!=6"
        :disabled="!operationFileList.length"
        @click="handleDeleteFileClick()"></el-button>
        <!-- disabled  | v-if  -->
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-rank"
        v-show="fileType!=6"
        :disabled="!operationFileList.length"
        v-if="fileType === 0"
        @click="handleMoveFileClick()"></el-button>
        <!-- -->
        <!-- <el-button
        size="mini"
        type="primary"
        icon="el-icon-rank"
        :disabled="!operationFileList.length"
        v-if="fileType === 0"
        @click="handleCopyFileClick()"></el-button> -->
        <!-- disabled  -->
       
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-download"
        v-show="fileType!=6"
        :disabled="!operationFileList.length"
        @click="handleDownloadFileClick()"></el-button>

        <el-button
        size="mini"
        type="primary"
        icon="el-icon-refresh-left"
        v-show="fileType==6"
        :disabled="!operationFileList.length"
        @click="handleRecoveryFileClick()"></el-button>

        <el-button
        size="mini"
        type="primary"
        icon="el-icon-delete"
        v-show="fileType==6"
        :disabled="!operationFileList.length"
        @click="handleDeleteRealFileClick()"></el-button>


      </el-button-group>

    <!--  -  -->
    <!-- @closed   -->
    <el-dialog
      title=""
      width="600px"
      :visible.sync="addFolderDialog.visible"
      @closed="$refs.addFolderForm.resetFields()"
    >
      <el-form
        :model="addFolderForm"
        :rules="addFolderRules"
        label-position="top"
        ref="addFolderForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addFolderForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addFolderDialog.visible = false"> </el-button>
        <el-button
          type="primary"
          :loading="addFolderDialog.loading"
          @click="handleAddFolderSubmit('addFolderForm')"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <a
      v-for="(item, index) in operationFileList"
      :key="index"
      :href="`/api/filetransfer/downloadfile?userFileId=${item.userFileId}`"
      :download="`${item.fileName}.${item.extendName}`"
      :ref="`downloadLink${index}`"
    ></a>
  </div>
</template>

<script >
import { createFile } from "@/request/file.js";
import { batchDeleteFile ,realDeleteFile, recoveryFile} from '@/request/file.js' //  

export default {
  name: "OperationMenu",
  props: {
    // 
    fileType: {
      type: Number,
      required: true,
    },
    // 
    filePath: {
      type: String,
      required: true,
    },
    //  
    operationFileList: {
        type: Array,
        required: true
    }
  },
  data() {
    var validateName = (rule, value, callback) => {
      let str = new RegExp(
            "[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']"
          );
        if (str.test(value)) {
          callback(new Error('*.?<|/\\'));
        }
        if(value.length>30||value.length<1){
          callback(new Error('1~30'));
        }
        callback(); 
      };
    return {
      // 
      addFolderDialog: {
        visible: false, //  
        loading: false,
      },
      // 
      addFolderForm: {
        name: "",
      },
      // 
      addFolderRules: {
        name: [
          { required: true,message: "",trigger: "blur, change",},
          {validator:validateName,trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    //  - 
    handleUploadFileClick() {
      this.$emit('handleUploadFile', true)
    },
    //  - 
    handleAddFolderSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addFolderDialog.loading = true; //  loading
          //  - 
          createFile({
            fileName: this.addFolderForm.name,
            filePath: this.filePath, //  
            isDir: 1, //  1  0 
          }).then(
            (res) => {
              this.addFolderDialog.loading = false; //  loading
              if (res.success) {
                this.$message.success("");
                this.addFolderDialog.visible = false; //  
                this.$emit("getTableData"); // 
              } else {
                this.$message.warning(res.message);
              }
            },
            (error) => {
              this.addFolderDialog.loading = false; //  loading
              console.log(error);
            }
          );
        } else {
          return false;
        }
      });
    },

    //
    handleDeleteRealFileClick(){
       // 
       this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          //   
          realDeleteFile({
            files: JSON.stringify(this.operationFileList)
          }).then((res) => {
            if (res.success) {
              this.$message({
                message: res.message,
                type: 'success'
              })
              this.$emit('getTableData') //  
            } else {
              this.$message.error('' + res.message)
            }
          })
        })
        .catch(() => {
          //  
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },

    //  - 
    handleDeleteFileClick() {
      // 
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          //   
          batchDeleteFile({
            files: JSON.stringify(this.operationFileList)
          }).then((res) => {
            if (res.success) {
              this.$message({
                message: res.message,
                type: 'success'
              })
              this.$emit('getTableData') //  
            } else {
              this.$message.error('' + res.message)
            }
          })
        })
        .catch(() => {
          //  
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },

    //
    handleRecoveryFileClick(){
       // 
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          
          //  
          recoveryFile({
            files: JSON.stringify(this.operationFileList)
          }).then((res) => {
            if (res.success) {
              this.$message({
                message: res.message,
                type: 'success'
              })
              this.$emit('getTableData') //  
            } else {
              this.$message.error('' + res.message)
            }
          })
        })
        .catch(() => {
          //  
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },

    //  - 
    handleMoveFileClick() {
        // true/false / | this.operationFileList 
        this.$emit('handleSelectFile', true, this.operationFileList)
        this.$emit('handleMoveFile', true) // true/false /
    },
    //
    handleCopyFileClick(){
        this.$emit('handleSelectFile', true, this.operationFileList)
        this.$emit('handleCopyMoveFile', true) // true/false /
    },
    //  - 
    handleDownloadFileClick() {
      for (let i = 0; i < this.operationFileList.length; i++) {
        this.$refs[`downloadLink${i}`][0].click() //   a 
      }
    },
  },
};
</script>