<template>
  <el-menu
    class="header-menu"
    :default-active="activeIndex"
    :router="true"
    mode="horizontal"
  >
  <el-menu-item
  index="Home"
  :route="{ name: 'Home', query: { fileType: 0, filePath: '/' } }"></el-menu-item>
    <el-menu-item
      class="login"
      index="Login"
      :route="{ name: 'Login' }"
      v-show="!isLogin"
      ></el-menu-item
    >
    <el-menu-item
      class="register"
      index="Register"
      :route="{ name: 'Register' }"
      v-show="!isLogin"
      ></el-menu-item
    >
    <!--  el-menu-item -->
    <div class="el-menu-item exit" @click="exitButton()" v-show="isLogin">
      
    </div>
    <!--  el-menu-item -->
    <div class="el-menu-item username" v-show="isLogin">
      <!-- Element UI -->
      <i class="el-icon-user-solid"></i>{{ username }}
    </div>
  </el-menu>
</template>

<script>
import Cookies from "js-cookie";

export default {
  name: "Header",
  data() {
    return {};
  },
  computed: {
    //  index
    activeIndex() {
      return this.$route.name; //  
    },
    // 
    isLogin() {
      return this.$store.getters.isLogin;
    },
    // 
    username() {
      return this.$store.getters.username;
    },
  },
  methods: {
    // 
    exitButton() {
      Cookies.set("token", "");
      this.$router.push({ path: "/login" }); //  
      this.$message.success("");
    },
  },
};
</script>

<style lang="stylus" scoped>
.header-menu {
  padding: 0 24px;

  .login, .register, .username, .exit {
    float: right;
  }
}
</style>