<template>
 <div class="home">
    <!--  -  -->
    <SideMenu class="home-left" :storageValue="storageValue"></SideMenu>
    <ImgReview></ImgReview>
    <!--  -->
    <div class="home-right">
      <div class="operation-wrapper">
        <OperationMenu
          :fileType="fileType"
          :filePath="filePath"
          :operationFileList="operationFileList"
          @handleSelectFile="setOperationFile"
          @handleMoveFile="setMoveFileDialog"
          @handleCopyMoveFile="setCopyFileDialog"
          @getTableData="getFileData"
          @handleUploadFile="handleUploadFile"></OperationMenu>
          <ShowModel :fileType="fileType"></ShowModel>
        <SelectColumn></SelectColumn>
      </div>
       <!--  -  -->
       <BreadCrumb :fileType="fileType"></BreadCrumb>
      <!--  -  -->
      <!--  -  -->
      <FileTable
        v-if="(fileType === 1 && showModel === 0) || fileType !== 1"
        :tableData="tableData"
        :loading="loading"
        :fileType="fileType"
        @getTableData="getFileData"
        @handleSelectFile="setOperationFile"
        @handleMoveFile="setMoveFileDialog"
        @handleCopyFile="setCopyFileDialog"></FileTable>
      <!--  v-if "" -->
      <FileGrid
        v-if="fileType === 1 && showModel === 1"
        :tableData="tableData"
        :loading="loading"></FileGrid>
      <!--  v-if "" -->
      <FileTimeLine
        v-if="fileType === 1 && showModel === 2"
        :tableData="tableData"
        :loading="loading"></FileTimeLine>
      <FilePagination
        :pageData="pageData"
        @changePageData="changePageData"
      ></FilePagination>
    </div>
    <FileUploader ref="globalUploader" @getTableData="getFileData"></FileUploader>
    <MoveFileDialog
      :dialogMoveFile="dialogMoveFile"
      @setSelectFilePath="setSelectFilePath"
      @confirmMoveFile="confirmMoveFile"
      @handleMoveFile="setMoveFileDialog"
    ></MoveFileDialog>

    <CopyFileDialog
      :dialogCopyFile="dialogCopyFile"
      @setSelectFilePath="setSelectFilePath"
      @confirmCopyFile="confirmCopyFile"
      @handleCopyFile="setCopyFileDialog"
    ></CopyFileDialog>
  </div>
</template>

<script>
import SideMenu from "./components/SideMenu.vue"; //  
import BreadCrumb from "./components/BreadCrumb.vue"; //  
import FileTable from "./components/FileTable.vue"; //  
import FilePagination from './components/FilePagination.vue' //  
import SelectColumn from './components/SelectColumn.vue' //  
import OperationMenu from './components/OperationMenu.vue' //  
import FileUploader from './components/FileUploader.vue' //   
import MoveFileDialog from './components/MoveFileDialog' //  
import ShowModel from './components/ShowModel.vue'//
import FileGrid from './components/FileGrid.vue' //
import FileTimeLine from "./components/FileTimeLine.vue"; //   
import ImgReview from "./components/ImgReview.vue"; //   
import CopyFileDialog from './components/CopyFileDialog'//


import { moveFile,copyFile } from '@/request/file.js' //  
import { batchMoveFile ,batchCopyFile} from '@/request/file.js'
import { getFileStorage } from '@/request/file.js'
import { getFileListByPath, getFileListByType } from '@/request/file.js' //  
import { getFileTree } from '@/request/file.js' //    

export default {
  inject:['reload'],
  name: "Home",
  components: {
    SideMenu, 
    BreadCrumb,
    FileTable,
    FilePagination,
    SelectColumn,
    OperationMenu,
    FileUploader,
    MoveFileDialog,
    CopyFileDialog,
    ShowModel,
    FileGrid,
    ImgReview,
    FileTimeLine,

    
  },
  data() {
    return {
      storageValue: 0, //  
      loading: false,
      tableData: [], //  
      pageData: {
        currentPage: 1, //   
        pageCount: 20, //  
        total: 0 //  
      },
      //  
      dialogMoveFile: {
        visible: false, //  
        fileTree: [] //  
      },
      //  
      dialogCopyFile: {
        visible: false, //  
        fileTree: [] //  
      },
      isBatch: false, //  
      operationFile: {}, // 
      operationFileList: [], // 
      selectFilePath: '', //  
    }
  },
  computed: {
    // 
    fileType() {
     
      return this.$route.query.fileType ? Number(this.$route.query.fileType) : 0;
    },
    // 
    filePath() {
        return this.$route.query.filePath;
    },
    // 
    showModel() {
      return this.$store.getters.showModel
    },
  },
  watch: {
    filePath() {
      // 
      if (this.fileType === 0) {
        this.getFileData(); //  
      }
    },
    fileType() {
      this.getFileData() //  
    }
},
  mounted() {
    this.getFileData(); //  
  },
  methods: {
    // 
    getFileData() {
        this.loading = true; // loading
        if (this.fileType === 0) {
          //   
          this.loading = true;
          this.getFileDataByPath();
        } else {
          //   
          this.getFileDataByType();
        }
        this.getStorageValue(); //  2.
      },
    // 
    getStorageValue() {
      getFileStorage().then((res) => {
        //    3. 
        if (res.success) {
          this.storageValue = res.data ? res.data : 0 //    4.
        } else {
          this.$message.error(res.message)
        }
      })
    },
   // 
   getFileDataByPath() {
        getFileListByPath({
            filePath: this.filePath, // 
            currentPage: this.pageData.currentPage,
            pageCount: this.pageData.pageCount
        }).then(
        (res) => {
          this.loading = false //  loading
          if (res.success) {
            this.tableData = res.data.list // 
            this.pageData.total = res.data.total //   - 
          } else {
            this.$message.error(res.message)
          }
        },
        (error) => {
          this.loading = false
          console.log(error)
        }
      )
    },
    // 
    getFileDataByType() {
      getFileListByType({
        fileType: this.fileType, //  
        currentPage: this.pageData.currentPage, //  
        pageCount: this.pageData.pageCount //  
      }).then(
        (res) => {
          this.loading = false //  loading
          if (res.success) {
            this.tableData = res.data.list // 
            this.pageData.total = res.data.total //   - 
          } else {
            this.$message.error(res.message)
          }
        },
        (error) => {
          this.loading = false
          console.log(error)
        }
      )
    },
    //  - 
    changePageData(pageData) {
      this.pageData.currentPage = pageData.currentPage // 
      this.pageData.pageCount = pageData.pageCount //  
      this.getFileData() // 
    },
    //  
    handleUploadFile() {
        //  
        this.$refs.globalUploader.triggerSelectFileClick()
    },
    /**
     * 
     * @param {Boolean} isBatch true false 
     * @param {Object | Array} file OjectArray
     */
     setOperationFile(isBatch, file) {
      this.isBatch = isBatch //  
      if (isBatch) {
        this.operationFileList = file //  
      } else {
        this.operationFile = file //  
      }
    },
    /**
     * 
     * @param {Boolean} visible /
     */
    setMoveFileDialog(visible) {
      this.dialogMoveFile.visible = visible //  
      if (visible) {
        // 
        getFileTree().then((res) => {
          if (res.success) {
            this.dialogMoveFile.fileTree = [res.data]
          } else {
            this.$message.error(res.message)
          }
        })
      }
    },

 /**
     * 
     * @param {Boolean} visible /
     */
     setCopyFileDialog(visible) {
      this.dialogCopyFile.visible = visible //  
      if (visible) {
        // 
        getFileTree().then((res) => {
          if (res.success) {
            this.dialogCopyFile.fileTree = [res.data]
          } else {
            this.$message.error(res.message)
          }
        })
      }
    },
    //
    confirmCopyFile() {
      if (this.isBatch) {
        //  
         let data = {
          filePath: this.selectFilePath,
          files: JSON.stringify(this.operationFileList)
        }
        batchCopyFile(data).then((res) => {
          if (res.success) {
            this.$message.success(res.message)
            this.getFileData() //  
            this.dialogCopyFile.visible = false //  
            this.operationFileList = []
          } else {
            this.$message.error(res.message)
          }
        })
      } else {
        //  
        let data = {
          filePath: this.selectFilePath, //  
          oldFilePath: this.operationFile.filePath, //  
          fileName: this.operationFile.fileName, //  
          extendName: this.operationFile.extendName //  
        }
        copyFile(data).then((res) => {
          if (res.success) {
            this.$message.success(res.message)
            this.getFileData() //  
            this.dialogCopyFile.visible = false //  
          } else {
            this.$message.error(res.message)
          }
        })
      }
    },
    //  
    setSelectFilePath(selectFilePath) {
      this.selectFilePath = selectFilePath
    },
    //  -
    confirmMoveFile() {
      if (this.isBatch) {
        //  
        let data = {
          filePath: this.selectFilePath,
          files: JSON.stringify(this.operationFileList)
        }
        batchMoveFile(data).then((res) => {
          if (res.success) {
            this.$message.success(res.data)
            this.getFileData() //  
            this.dialogMoveFile.visible = false //  
            this.operationFileList = []
          } else {
            this.$message.error(res.message)
          }
        })
      } else {
        //  
        let data = {
          filePath: this.selectFilePath, //  
          oldFilePath: this.operationFile.filePath, //  
          fileName: this.operationFile.fileName, //  
          extendName: this.operationFile.extendName //  
        }
        moveFile(data).then((res) => {
          if (res.success) {
            this.$message.success('')
            this.getFileData() //  
            this.dialogMoveFile.visible = false //  
          } else {
            this.$message.error(res.message)
          }
        })
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.home {
  // flex  
  display: flex;

  .home-left {
    box-sizing: border-box;
  }

  .home-right {
    box-sizing: border-box;
    width: calc(100% - 200px);
    padding: 8px 24px;
    flex: 1;

    .operation-wrapper {
      margin-bottom: 16px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      //  
      >>> .operation-menu-wrapper {
          flex: 1;
      }
    }
  }
}
</style>