<template>
  <div id="global-uploader">
    <!--  -->
    <uploader
      class="uploader-box"
      ref="uploader"
      :options="options"
      :autoStart="false"
      @file-added="handleFileAdded"
      @file-progress="onFileProgress"
      @file-success="handleFileSuccess"
      @file-error="handleFileError"
    >
      <uploader-unsupport></uploader-unsupport>
      <!--  -->
      <uploader-btn class="select-file-btn" :attrs="attrs" ref="uploadBtn"
        ></uploader-btn
      >
      <!--  -->
      <uploader-list v-show="panelShow">
        <div class="file-panel" slot-scope="props">
          <div class="panel-title">
            <span class="text"></span>
            <div class="operate-btn-wrapper">
              <el-button
                type="text"
                title=""
                icon="el-icon-close"
                @click="handleClosePanel"
              >
              </el-button>
            </div>
          </div>
          <!--  -->
          <ul class="file-list">
            <li class="file-item" v-for="file in props.fileList" :key="file.id">
              <uploader-file
                ref="files"
                :class="`file_${file.id}`"
                :file="file"
                :list="true"
              ></uploader-file>
            </li>
            <div class="no-file" v-if="!props.fileList.length">
              <i class="icon-empty-file"></i> 
            </div>
          </ul>
        </div>
      </uploader-list>
    </uploader>
  </div>
</template>

<script>
import SparkMD5 from "spark-md5";
import Cookies from "js-cookie";

export default {
  name: "FileUploader",
  data() {
    return {
      //  https://github.com/simple-uploader/Uploader/blob/develop/README_zh-CN.md#uploader
      options: {
        target: "api/filetransfer/uploadfile", //  URL
        chunkSize: 1024 * 1024, //  
        fileParameterName: "file", //   file
        maxChunkRetries: 3, //   3
        testChunks: true, //  
        // 
        checkChunkUploadedByResponse: function (chunk, message) {
          let data = JSON.parse(message).data;
          if (data.skipUpload) {
            // 
            return true;
          }
          return (data.uploaded || []).indexOf(chunk.offset + 1) >= 0;
        },
        headers: {
          token: Cookies.get("token"),
        },
        query() {},
      },
      attrs: {
        accept: "*", // 
      },
      panelShow: false, //  
    };
  },
  computed: {
    //  uploader
    uploader() {
      return this.$refs.uploader.uploader;
    },
    // 
    filePath() {
      return this.$route.query.filePath ? this.$route.query.filePath : "/";
    },
    // 
    startUploadFile() {
      return this.$store.state.file.startUploadFile;
    },
  },
  methods: {
    // 
    triggerSelectFileClick() {
      this.$refs.uploadBtn.$el.click(); //   
    },
    //  
    handleFileAdded(file) {
      this.panelShow = true; //  
      this.calculateFileMD5(file); //  MD5
    },
    /**
     *  
     * @param rootFile  Uploader.File 
     * @param file  Uploader.File 
     * @param chunk Uploader.Chunk chunk.xhr.status 
     */
    onFileProgress(rootFile, file, chunk) {
      // 
      console.log(
        ` ${file.name}chunk${chunk.startByte / 1024 / 1024} ~ ${
          chunk.endByte / 1024 / 1024
        }`
      );
    },
    /**
     *  
     * @param responseStr 
     */
    handleFileSuccess(rootFile, file, responseStr) {
      let response = JSON.parse(responseStr);
      if (response.success) {
        this.$message.success(`${file.name} - `);
        this.$emit("getTableData"); //  
      } else {
        this.$message.error(response.message);
      }
    },
    //  
    handleFileError(rootFile, file, responseStr) {
      this.$message({
        message: JSON.parse(responseStr).message,
        type: "error",
      });
    },
    // MD5
    calculateFileMD5(file) {
      let fileReader = new FileReader();
      let time = new Date().getTime();
      let blobSlice =
        File.prototype.slice ||
        File.prototype.mozSlice ||
        File.prototype.webkitSlice;
      let currentChunk = 0;
      const chunkSize = 1 * 1024 * 1024;
      let chunks = Math.ceil(file.size / chunkSize);
      let spark = new SparkMD5.ArrayBuffer();
      file.pause(); //  
      loadNext();
      fileReader.onload = (e) => {
        spark.append(e.target.result);
        if (currentChunk < chunks) {
          currentChunk++;
          loadNext();
        } else {
          let md5 = spark.end();
          this.calculateFileMD5End(md5, file);
          console.log(
            `MD5${file.name} \nMD5${md5} \n${chunks} :${
              file.size
            } ${new Date().getTime() - time} ms`
          );
        }
      };
      fileReader.onerror = function () {
        this.$notify({
          title: "",
          message: `${file.name}`,
          type: "error",
          duration: 2000,
        });
        file.cancel(); //  
      };
      function loadNext() {
        let start = currentChunk * chunkSize;
        let end =
          start + chunkSize >= file.size ? file.size : start + chunkSize;
        fileReader.readAsArrayBuffer(blobSlice.call(file.file, start, end));
      }
    },
    // MD5
    calculateFileMD5End(md5, file) {
      // uploaderopts
      Object.assign(this.uploader.opts, {
        query: {
          filePath: this.filePath,
          isDir: 0, // isDir  0 
        },
      });
      file.uniqueIdentifier = md5;
      file.resume(); //  
    },
    // 
    handleClosePanel() {
      this.uploader.cancel(); //  
      this.panelShow = false;
    },
  },
};
</script>

<style lang="stylus" scoped>
#global-uploader {
  position: fixed;
  z-index: 20;
  right: 15px;
  bottom: 15px;

  .uploader-box {
    width: 520px;
  }

  .file-panel {
    background-color: #fff;
    border: 1px solid #e2e2e2;
    border-radius: 7px 7px 0 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

    .panel-title {
      display: flex;
      height: 40px;
      line-height: 40px;
      padding: 0 16px;
      border-bottom: 1px solid #ddd;

      .text {
        padding-left: 0;
        margin-bottom: 0;
        font-size: 16px;
        color: #303133;
      }

      .operate-btn-wrapper {
        flex: 1;
        text-align: right;

        >>> .el-button {
          *:hover {
            opacity: 0.5;
          }

          i[class^=el-icon-] {
            color: #000;
          }
        }
      }
    }

    .file-list {
      position: relative;
      height: 240px;
      overflow-x: hidden;
      overflow-y: auto;
      background-color: #fff;
      font-size: 14px;

      .file-item {
        background-color: #fff;

        >>>.uploader-file-icon {
          display: none;
        }
      }
    }
  }

  .no-file {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 16px;
  }

  /deep/.uploader-file-actions > span {
    margin-right: 6px;
  }
}

/*  */
.select-file-btn {
  display: none;
}
</style>